@media screen and (max-width: 1200px), screen and (max-device-width: 1200px){
  body{
    .content{
      .container{
        .row{
          .col-md-3{
            .aside{
              &__vhLogo{
                p{
                  padding: 0 0 0 125px;
                  font-size: 2.063em;
                }
              }
              &__examples{
                h2{
                  font-size: .813em;
                }
              }
            }
          }
        }
      }
    }
  }
}


@media screen and (max-width: 992px), screen and (max-device-width: 992px){
  body{
    .content{
      .container{
        .row{
          .col-sm-12{
            .aside{
              flex-wrap: wrap;
              justify-content: space-between;
              &__vhLogo,
              &__menu{
                width: 100%;
              }
              &__bestHolo,
              &__newHolo,
              &__threeD,
              &__examples{
                width: 49%;
              }
              &__vhLogo{
                flex-direction: row;
                justify-content: center;
                align-items: center;
                background-image: url("../img/vh_logo_large.jpg");
                p{
                  padding: 0 15px;
                  font-size: 3em;
                  &:nth-child(1){
                    font-size: 3em;
                  }
                }
              }
              &__menu{
                ul{
                  li{
                    a{
                      padding: 0;
                      text-align: center;
                    }
                  }
                }
              }
              &__examples{
                h2{
                  font-size: .938em;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 585px), screen and (max-device-width: 585px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .aside{
              &__bestHolo,
              &__newHolo,
              &__threeD,
              &__examples{
                width: 100%;
              }
              &__vhLogo{
                flex-direction: column;
                justify-content: center;
                align-items: unset;
                background-image: url("../img/vh_logo.jpg");
                p{
                  padding: 0 0 0 55%;
                  font-size: 2.438em;
                  &:nth-child(1){
                    font-size: 1.625em;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}