@import "components/m_header";
@import "components/m_asside";
@import "components/m_footer";

@media screen and (max-width: 1200px), screen and (max-device-width: 1200px){
  body{
    .content{
      .container{
        .row{
          .col-md-12{
            .logo{
              background-position: -40px;
            }
          }
          .col-md-9{
            .customize{
              &__secondStep{
                .hologram{
                  ul{
                    li{
                      .hologramPreview{
                        .layer-3{
                          .bg{
                            width: 235px;
                            height: 110px;
                            ul{
                              .userLogo{
                                img{
                                  width: 75px;
                                  height: 80px;
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
                .hologramOptions{
                  .layer-1,
                  .layer-2,
                  .layer-3{
                    .companyName,
                    .companySecurity,
                    .companyOriginal,
                    .companyTop{
                      .name{
                        input{
                          width: 190px;
                        }
                      }
                      .color{
                        width: 100px;
                      }
                    }
                    .selectFile{
                      input + label{
                        margin: 0 35px 0 0;
                      }
                    }
                  }
                }
              }
              &__thirdStep{
                .hologram{
                  .hologramPreview{
                    .layer-3{
                      .bg{
                        ul{
                          .userLogo{
                            img{
                              width: 95px;
                              height: 110px;
                            }
                          }
                        }
                      }
                    }
                  }
                }
                .hologramOptions{
                  .layer-1,
                  .layer-2,
                  .layer-3{
                    .laserNumbering,
                    .laserLogo,
                    .laserText{
                      .name{
                        input{
                          width: 190px;
                        }
                      }
                      .fontSize{
                        margin: 0 0 0 80px;
                        p{
                          margin: 0;
                        }
                      }
                      .fontWeight{
                        p{
                          margin: 0 8px 0 0;
                        }
                      }
                    }
                    .selectFile{
                      input + label{
                        margin: 0 35px 0 0;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px), screen and (max-width: 992px){
  body{
    .content{
      .container{
        .row{
          .col-sm-12{
            .logo{
              p{
                padding: 0 35px 0 0;
                font-size: 2.25em
              }
            }
          }
          .col-md-3{
            .aside{
              display: none;
            }
          }
          .col-sm-12{
            .aside{
              margin: 35px 0 0;
              display: flex;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px), screen and (max-device-width: 767px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .customize{
              &__firstStep{
                .itemShapeCustom{
                  .itemsShape{
                    ul{
                      flex-wrap: wrap;
                      li{
                        margin: 10px 0;
                        width: 50%;
                      }
                    }
                  }
                }
              }
              &__secondStep{
                flex-direction: column;
                .hologram,
                .hologramOptions{
                  width: 100%;
                }
                .hologram{
                  margin: 15px 0 0;
                  padding: 0;
                  ul{
                    li{
                      width: 325px;
                      .arrowUp,
                      .arrowDown{
                        display: none;
                      }
                    }
                  }
                }
                .hologramOptions{
                  padding: 0 10px;
                  .layer-1,
                  .layer-2,
                  .layer-3{
                    .companyName,
                    .companySecurity,
                    .companyOriginal,
                    .companyTop{
                      justify-content: space-around;
                      .name{
                        width: 100%;
                        input{
                          width: 100%;
                        }
                      }
                    }
                    .selectFile{
                      flex-wrap: wrap;
                      justify-content: space-around;
                      input + label{
                        margin: 0 0 10px 0;
                        width: 100%;
                      }
                    }
                  }
                }
              }
              &__thirdStep{
                flex-direction: column;
                .hologram,
                .hologramOptions{
                  width: 100%;
                  .hologramPreview{
                    width: 325px;
                  }
                }
                .hologram{
                  padding: 0;
                  ul{
                    li{
                      .arrowUp,
                      .arrowDown{
                        display: none;
                      }
                    }
                  }
                }
                .hologramOptions{
                  padding: 0 10px;
                  .layer-1,
                  .layer-2,
                  .layer-3{
                    .laserNumbering,
                    .laserLogo,
                    .laserText{
                      justify-content: space-around;
                      .name{
                        width: 100%;
                        input{
                          width: 100%;
                        }
                      }
                      .selectFile{
                        padding: 0;
                      }
                      .fontSize{
                        width: unset;
                      }
                      .fontWeight{
                        p{
                          margin: 0 8px 0 0;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 700px), screen and (max-device-width: 700px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .logo{
              display: flex;
              justify-content: flex-end;
              background-position: -20px;
              p{
                margin: 0 35px 0 0;
                padding: 0;
                width: 100%;
                font-size: 1.75em;
                line-height: 60px;
              }
            }
            .customize{
              &__thirdStep{
                .hologramOptions{
                  .layer-2{
                    .laserLogo{
                      .fontSize{
                        margin: 0;
                        width: 70%;
                        justify-content: center;
                      }
                      .fontWeight{
                        width: 30%;
                        justify-content: flex-start!important;
                        p{
                          margin: 0 8px 0 0;
                        }
                      }
                    }
                  }
                }
              }
              .backOrForward{
                a{
                  padding: 0;
                  width: 45%;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 650px), screen and (max-device-width: 650px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .customize{
              &__firstStep{
                ul{
                  li{
                    a{
                      padding: 20px 25px;
                      font-size: 1em;
                      line-height: 1.25em;
                    }
                  }
                }
                .itemShape{
                  flex-direction: column;
                  .item{
                    width: 100%;
                    flex-direction: column;
                    justify-content: center;
                    .shapeDescr{
                      margin: 0;
                      width: 250px;
                      display: flex;
                      justify-content: space-around;
                      ul{
                        justify-content: flex-start;
                        li{
                          margin: 5px 0 0;
                        }
                      }
                    }
                  }
                }
              }
              &__thirdStep{
                .hologramOptions{
                  .layer-1,
                  .layer-2,
                  .layer-3{
                    .laserNumbering,
                    .laserLogo,
                    .laserText{
                      .selectFile{
                        flex-wrap: wrap;
                        justify-content: space-around;
                        input + label{
                          margin: 0 0 10px 0;
                          width: 100%;
                        }
                      }
                      .color{
                        width: 100%;
                        justify-content: center;
                      }
                      .fontSize,
                      .fontWeight{
                        justify-content: center;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

