@media screen and (max-width: 1200px), screen and (max-device-width: 1200px){
  body{
    font-size: 14px;
    line-height: 16px;
    .header{
      &__headline{
        height: 115px;
        .container{
          .row{
            .col-md-12{
              .headline{
                height: 115px;
                .logo{
                  a{
                    width: 285px;
                  }
                  div{
                    p{
                      &:nth-child(1){
                        font-size: 1em;
                      }
                      &:nth-child(2){
                        font-size: 1.24em;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px), screen and (max-device-width: 992px){
  body{
    font-size: 16px;
    line-height: 18px;
    .header{
      &__headline{
        .container{
          .row{
            .col-sm-12{
              .headline{
                .logo{
                  width: 100%;
                }
                .search{
                  display: none;
                }
              }
            }
          }
        }
      }
      &__mainNav{
        .container{
          .row{
            .col-sm-12{
              .mainNav{
                display: none;
              }
              .mobile__mainNav{
                display: flex;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px), screen and (max-device-width: 767px){
  body{
    font-size: 14px;
    line-height: 16px;
    .header{
      &__nav{
        .container{
          .row{
            .col-xs-1{
              .nav{
                display: none;
              }
              .mobile__nav{
                display: block;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 450px), screen and (max-device-width: 450px){
  body{
    .header{
      &__headline{
        .container{
          .row{
            .col-xs-12{
              .headline{
                .logo{
                  div{
                    display: none;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}